<template>
  <div class="badge" :class="color">
      {{ value }}
  </div>
</template>

<script>
export default {
    name: 'Badge',
    props: ['value'],
    computed: {
        color() {
            switch(this.value) {
                case 'baixo':
                case 'Baixo':
                    return 'green';
                case 'mdio':
                case 'Mdio':
                    return 'yellow';
                case 'alto':
                case 'Alto':
                    return 'red';

                case '0 - Sem vulnerabilidades':
                case 'O - Sem Vulnerabilidade':
                    return 'blue';
                case 'A - vulnerabilidade financeira':
                case 'A - Vulnerabilidade Financeira':
                case 'B - vulnerabilidade alimentar':
                case 'B - Vulnerabilidade Alimentar':
                    return 'yellow';
                case 'C - Situao de violncia':
                case 'C - situao de violncia':
                    return 'red';

                case 'Ia - Assintomtico, mora com assintomticos':
                case 'Ib - Assintomtico, mas vive sozinho':
                    return 'blue';
                case 'IIa - Assintomtico, mas sai de casa':
                case 'IIb - Assintomtico, mas recebe visitas ou domiciliares saem':
                case 'IIIa - Assintomtico, mas com comorbidades':
                    return 'green';
                case 'IIIb - Assintomtico, mas tem contato com sintomticos ou confirmados':
                case 'IVa - Assintomtico, mas sem medicaes':
                    return 'yellow';
                case 'IVb - Idoso sintomtico':
                    return 'red';

                case '0 - No atendeu  ligao':
                default:
                    return 'gray'; 
            }
        }
    }
}
</script>

<style>
    .badge {
        background-color: darkgray;
        color: white;
    }

    .badge.red {
        background-color: rgb(235, 87, 87);
    }

    .badge.green {
        background-color: rgb(39, 174, 96);
    }

    .badge.blue {
        background-color: rgb(45, 156, 219);
    }

    .badge.yellow {
        background-color: rgb(242, 201, 76);
    }

    .badge.gray {
        background-color: rgb(96, 96, 96);
    }
</style>